import { Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';

import { MessageService } from 'primeng/api';
import { ItemService } from 'src/app/services/criacoes/item/item.service';

import { DetalharService } from 'src/app/services/hall/detalhar/detalhar.service';
import { PresentAtaqueComponent } from '../../utils/present-ataque/present-ataque.component';
import { PresentDefesaComponent } from '../../utils/present-defesa/present-defesa.component';
import { PresentUtilizavelComponent } from '../../utils/present-utilizavel/present-utilizavel.component';

@Component({
  selector: 'app-item-detail',
  templateUrl: './item-detail.component.html',
  styleUrls: ['./item-detail.component.scss'],
})
export class ItemDetailComponent implements OnInit {

  item;
  loading = true;
  tipoItem;


  constructor(
    private modal: ModalController,
    private modalInterno: ModalController,
    private detalharService: DetalharService,
    private itemService: ItemService,
    private toast: MessageService,
  ) {

  }

  ngOnInit() {
    const id = this.detalharService.getId();
    this.tipoItem = this.detalharService.getTipoItem();

    this.itemService.findById(id, this.tipoItem)
      .subscribe(res => {
        console.log(res);
        this.item = res;
        this.loading = false;
      },
        error => {
          console.log(error);
          this.loading = false;
        });
  }


  close() {
    this.modal.dismiss(null, null, "item-detail");
  }

  mudaAba(event) {
  }

  callToast(severity, summary, detail) {
    this.toast.clear();
    this.toast.add({
      severity: severity,
      summary: summary,
      detail: detail
    });
  }

  async openModal(event) {

    let component;

    switch(this.tipoItem){
      case 0:
      component = PresentAtaqueComponent;
      break;

      case 1:
        component = PresentDefesaComponent;
      break;

      case 2:
        component = PresentUtilizavelComponent;
      break;
    }

    const modal = await this.modalInterno.create({
      component,
      componentProps: {
        item: this.item
      },
      backdropDismiss: false

    });

    return await modal.present();
  }

}
