import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { API_CONFIG } from 'src/app/config/api.config';
import { StorageService } from '../../storage-service.service';
import { GenericHallDTO } from 'src/app/models/hall/generic-hall.dto';

@Injectable({
  providedIn: 'root'
})
export class ItemPageService {

  constructor(private http: HttpClient, private storage: StorageService) { }


  findPageByNewerAtaque(page: string = '0', linesPerPage: string = '10'): Observable<any> {
    const params = new HttpParams()
      .set('page', page)
      .set('linesPerPage', linesPerPage);

    return this.http.get(`${API_CONFIG.baseUrl}page/item/newer/ataque`,
      {
        params,
        observe: 'body',
      });
  }

  findPageBySearchAtaque(page: string = '0', descricao: string, orderBy: string, direction: string, linesPerPage: string = '10'): Observable<any> {
    const params = new HttpParams()
      .set('page', page)
      .set('descricao', descricao)
      .set('orderBy', orderBy)
      .set('direction', direction)
      .set('linesPerPage', linesPerPage);

    return this.http.get(`${API_CONFIG.baseUrl}page/item/search/ataque`,
      {
        params,
        observe: 'body',
      });
  }

  findPageByNewerDefesa(page: string = '0', linesPerPage: string = '10'): Observable<any> {
    const params = new HttpParams()
      .set('page', page)
      .set('linesPerPage', linesPerPage);

    return this.http.get(`${API_CONFIG.baseUrl}page/item/newer/defesa`,
      {
        params,
        observe: 'body',
      });
  }

  findPageBySearchDefesa(page: string = '0', descricao: string, orderBy: string, direction: string, linesPerPage: string = '10'): Observable<any> {
    const params = new HttpParams()
      .set('page', page)
      .set('descricao', descricao)
      .set('orderBy', orderBy)
      .set('direction', direction)
      .set('linesPerPage', linesPerPage);

    return this.http.get(`${API_CONFIG.baseUrl}page/item/search/defesa`,
      {
        params,
        observe: 'body',
      });
  }

  findPageByNewerUtilizavel(page: string = '0', linesPerPage: string = '10'): Observable<any> {
    const params = new HttpParams()
      .set('page', page)
      .set('linesPerPage', linesPerPage);

    return this.http.get(`${API_CONFIG.baseUrl}page/item/newer/utilizavel`,
      {
        params,
        observe: 'body',
      });
  }

  findPageBySearchUtilizavel(page: string = '0', descricao: string, orderBy: string, direction: string, linesPerPage: string = '10'): Observable<any> {
    const params = new HttpParams()
      .set('page', page)
      .set('descricao', descricao)
      .set('orderBy', orderBy)
      .set('direction', direction)
      .set('linesPerPage', linesPerPage);

    return this.http.get(`${API_CONFIG.baseUrl}page/item/search/utilizavel`,
      {
        params,
        observe: 'body',
      });
  }


}
